import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Helmet } from "react-helmet"
import { Link } from "gatsby"
import "../components/base.css"

const MyMentor = () => {
  return (
    <Layout id="index_layout" location="mymentor">
      <Helmet title={"MyMentor"}>
        <link href="https://unpkg.com/nes.css@latest/css/nes.min.css" rel="stylesheet" type="text/css" />
      </Helmet>
      <SEO title="MyMentor" />
      <div className="flex-column">
        <Link
          to="/"
          style={{
            color: `rgb(247, 140, 108)`,
            fontFamily: `Press Start 2P, cursive`,
            fontSize: `1.3rem`,
            fontWeight: 800,
            marginBottom: `1rem`,
            textDecoration: `none`,
          }}
        >
          Andrew Stolzle
        </Link>
        <article className="flex-column">
          <h1 style={{ color: `#50fa7b`, fontSize: `2rem` }}>MyMentor</h1>
          <div className="flex-row-wrap">
            <div className="boxed-text">Xamarin</div>
            <div className="boxed-text">Android</div>
            <div className="boxed-text">C#</div>
            <div className="boxed-text">SQL</div>
            <div className="boxed-text">.NET</div>
          </div>
          <p>
            This was a project originally designed for my University computer science course. The goal of MyMentor is to
            provide a platform that teachers can use to help mentor students in both their inter- and extra-curricular
            needs. MyMentor aims to foster professional, yet personal relationships between students and teachers. These
            relationships will help students throughout their college journey and beyond by allowing them to easily
            communicate with peers and professors so that they have access to all of the support they need to succeed at
            their University.
          </p>
          <figure>
            <img src={require("../images/login.png")} alt="login screen" />
            <figcaption>Login</figcaption>
          </figure>
          <h3>Design and Structure</h3>
          <p>
            Two essential components guided the design of this product: an intuitive layout and consistent style across
            the app. MyMentor needed to have simple colors, bottom navigation, familiar buttons and icons.{" "}
            <em>I did not want to force the user to learn or adapt to new buttons, shapes, or actions.</em>
            <br />
            In order to create simpler and more robust interfaces, I utilized the model-view-viewmodel (MVVM) pattern, a
            practice that is easy to implement with Xamarin. Here are two of the main screens a user will interact with:
          </p>
          <div className="flex-row-wrap space-evenly">
            <figure>
              <img src={require("../images/home.png")} alt="home screen" />
              <figcaption>Home</figcaption>
            </figure>
            <figure>
              <img src={require("../images/events.png")} alt="events page" />
              <figcaption>Community events and posts</figcaption>
            </figure>
          </div>
          <h3>Main Features</h3>
          <p>
            MyMentor revolves around matching students and professors primarily by their shared academic interests (i.e.
            matching users by subjects they both like). Upon initial log-in, users are asked to select their favorite
            subject, which MyMentor then uses to find the best matches for that person.
          </p>
          <figure>
            <img src={require("../images/subject.png")} alt="favorite subject dialog" />
            <figcaption>Selecting your favorite subject</figcaption>
          </figure>
          <p>
            MyMentor allows users to communicate with each other with an instant message feature, which is accomplished
            via polling. Every 10 seconds, MyMentor polls a SQL database to check for any new messages for the logged-in
            user. Implementing push notifications would be an upgrade because with this method, as new messages would be
            delivered only when a new message is sent to the user. This prevents unecessary polling and database calls
            that the current method of polling relies on.
          </p>
          <figure>
            <img src={require("../images/chat.png")} alt="chat page" />
            <figcaption>Chat</figcaption>
          </figure>
          <p>
            Users can customize their profile by selecting which subjects they're interested in and by updating their
            display name. Profiles are visible to other users so that they can see what you are interested in and can
            then determine if you would be a good match for a mentorship. Users can also add new subjects to their
            profile after creating an account.
          </p>
          <div className="flex-row-wrap space-evenly">
            <figure>
              <img src={require("../images/profile.png")} alt="profile screen" />
              <figcaption>User profile</figcaption>
            </figure>
            <figure>
              <img src={require("../images/subjects.png")} alt="updating favorite subjects" />
              <figcaption>Updating your favorite subjects</figcaption>
            </figure>
          </div>
        </article>
      </div>
    </Layout>
  )
}

export default MyMentor
